@import './common/styles/sass/variables.scss';

* {
  box-sizing: border-box;
}

/* poppins-regular - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */
      url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */
      url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('./assets/fonts/Poppins_400/poppins-v15-latin-regular.svg')
      format('svg'); /* Legacy iOS */
}
/* poppins-600 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  src: url('./assets/fonts/Poppins_600/poppins-v15-latin-600.eot'); /* IE9 Compat Modes */
  src: local(''),
    url('./assets/fonts/Poppins_600/poppins-v15-latin-600.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('./assets/fonts/Poppins_600/poppins-v15-latin-600.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('./assets/fonts/Poppins_600/poppins-v15-latin-600.woff')
      format('woff'),
    /* Modern Browsers */
      url('./assets/fonts/Poppins_600/poppins-v15-latin-600.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('./assets/fonts/Poppins_600/poppins-v15-latin-600.svg') format('svg'); /* Legacy iOS */
}

h1,
h2,
h4 {
  font-family: 'Poppins', Helvetica, sans-serif;
  font-weight: 600;
  font-style: normal;
  color: $textColor_1;
}

h1 {
  font-size: 48px;
  line-height: 56px;
}

h2 {
  font-size: 21px;
  line-height: 1.2em;
  position: relative;
}

h2::after {
  background-image: -webkit-repeating-radial-gradient(
    center center,
    rgb(9, 174, 234),
    rgb(9, 174, 234) 1px,
    rgba(0, 0, 0, 0) 0px,
    rgba(0, 0, 0, 0) 100%
  );
  background-size: 6px 6px;
  box-sizing: border-box;
  content: '';
  display: block;
  height: 20px;
  left: -9px;
  opacity: 0.45;
  position: absolute;
  top: -5px;
  width: 20px;
  z-index: 0;
}

/* body h2:not(h2:last-child)::after {
  content: none
} */

h4 {
  font-size: 16px;
  line-height: 1.2;
  margin: 8px 0 8px 0;
}

p,
a {
  font-family: 'Poppins', Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.75em;
  color: $textColor_2;
  margin: 0 25px 10px 25px;
}
